import React , {useState} from 'react';
import {Link , useHistory} from 'react-router-dom';
import {useDispatch} from 'react-redux';
import * as profileActions from "../../../redux/profiles/profile.action";



let AddEducation = ()=>{

    let dispatch = useDispatch();
    let history = useHistory();
    let [education , setEducation] =useState({
        school : '',
        degree : '',
        fieldOfStudy :'',
        from : '',
        current : '',
        to : '',
        description:''
    });

    let updateInput = (e)=>{
        if(e.target.type === 'checkbox'){
            setEducation({
                ...education,
                [e.target.name] : e.target.checked
            });
        }
        else{
            setEducation({
                ...education,
                [e.target.name] : e.target.value
            });
        }
    };

    let submitExperience =(event)=>{
        event.preventDefault();
        dispatch(profileActions.addEducation(education , history));
    };
    return(
     <React.Fragment>
         <section className="p-3">
             <div className="container animated zoomIn">
                 <div className="row">
                     <div className="col">
                         <p className="h3 text-teal"><i className="fa fa-user-clock"/>Add Experience</p>
                         <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Libero maxime nulla quam quisquam quos sapiente veniam. Commodi corporis cumque doloremque excepturi explicabo illum ipsam, magnam natus repellat sunt ullam voluptatem!</p>
                     </div>
                 </div>
             </div>
         </section>
         <section>
             <div className="container">
                 <div className="row">
                     <div className="col-md-8">
                         <form onSubmit={submitExperience}>
                             <div className="input-group mb-3">
                                 <div className="input-group-prepend">
                                     <span className="input-group-text bg-light-grey text-teal">School</span>
                                 </div>
                                 <input required
                                        name="school"
                                        value={education.school}
                                        onChange={updateInput}
                                        type="text" className="form-control" placeholder="School"/>
                             </div>
                             <div className="input-group mb-3">
                                 <div className="input-group-prepend">
                                     <span className="input-group-text bg-light-grey text-teal">Degree</span>
                                 </div>
                                 <input required
                                        name="degree"
                                        value={education.degree}
                                        onChange={updateInput}
                                        type="text" className="form-control" placeholder="Degree"/>
                             </div>
                             <div className="input-group mb-3">
                                 <div className="input-group-prepend">
                                     <span className="input-group-text bg-light-grey text-teal">FOS</span>
                                 </div>
                                 <input required
                                        name="fieldOfStudy"
                                        value={education.fieldOfStudy}
                                        onChange={updateInput}
                                        type="text" className="form-control" placeholder="Field of Study"/>
                             </div>
                             <div className="input-group mb-3">
                                 <div className="input-group-prepend">
                                     <span className="input-group-text bg-light-grey text-teal">From Date</span>
                                 </div>
                                 <input required
                                        name="from"
                                        value={education.from}
                                        onChange={updateInput}
                                        type="date" className="form-control" placeholder="From Date"/>
                             </div>
                             <div className="form-check mb-3">
                                 <input
                                     name="current"
                                     value={education.current}
                                     onChange={updateInput}
                                     className="form-check-input" type="checkbox" id="defaultCheck1"/>
                                 <label className="form-check-label" htmlFor="defaultCheck1">
                                     Current
                                 </label>
                             </div>
                             <div className="input-group mb-3">
                                 <div className="input-group-prepend">
                                     <span className="input-group-text bg-light-grey text-teal">To Date</span>
                                 </div>
                                 <input required
                                        name="to"
                                        value={education.to}
                                        onChange={updateInput}
                                        type="date" className="form-control"/>
                             </div>
                             <div className="input-group mb-3">
                                 <div className="input-group-prepend">
                                     <span className="input-group-text bg-light-grey text-teal">Description</span>
                                 </div>
                                 <textarea required
                                           name="description"
                                           value={education.description}
                                           onChange={updateInput}
                                           rows="3" className="form-control" placeholder="Description"/>
                             </div>
                             <div>
                                 <input type="submit" value="add experience" className="btn btn-teal btn-sm"/>
                                 <Link to="/profiles/dashboard" className="btn bg-light-grey btn-sm">Back</Link>
                             </div>
                         </form>
                     </div>
                 </div>
             </div>
         </section>
     </React.Fragment>
    )
};
export  default AddEducation;