import React , {useState  , useEffect} from 'react';
import {useDispatch , useSelector} from 'react-redux';
import {Link} from 'react-router-dom';
import * as userReducer from '../../../redux/users/user.reducer';
import * as postActions from '../../../redux/post/post.actions';
import * as postReducer from '../../../redux/post/post.reducer';
import Spinner from '../../../layout/misc/spinner/Spinner';

let PostList = ()=>{

    let [localPost , setLocalPost] = useState({
        text : '',
        image : ''
    });

    let dispatch = useDispatch();

    let userInfo = useSelector((state) => {
        return state[userReducer.usersFeaturesKay];
    });

    let {user} = userInfo;

    let postInfo = useSelector((state) => {
        return state[postReducer.postFeaturesKey];
    });

    let {loading , posts} = postInfo;

    useEffect(() => {
        dispatch(postActions.getAppPost());
    }, []);

    let updateInput = (e) => {
        setLocalPost({
            ...localPost,
            [e.target.name] : e.target.value
        });
    };

    let submitCreatePost = (e) => {
        e.preventDefault();
        dispatch(postActions.createPost(localPost));
        setLocalPost({
            text : '',
            image: ''
        });
    };

    let clickDeletePost = (postId) => {
        dispatch(postActions.deletePost(postId));
    };

    let clickLikePost = (postId) => {
        dispatch(postActions.likePost(postId));
    };

    let clickUnLikePost = (postId) => {
        dispatch(postActions.unLikePost(postId));
    };
    return(
     <React.Fragment>
       <section className="p-3">
           <div className="container">
               <div className="row">
                   <div className="col">
                       <p className="h3">Welcome to React Social Community </p>
                       <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. A aliquam aliquid aspernatur assumenda eaque eveniet id iure nemo nostrum officiis quidem ratione, veniam, voluptatum. Accusamus dignissimos exercitationem molestias quas quibusdam!</p>
                   </div>
               </div>
           </div>
       </section>
         {
             Object.keys(user).length > 0 &&
             <section>
                 <div className="container">
                     <div className="row">
                         <div className="col-md-8">
                             <form  onSubmit={submitCreatePost}>
                                 <div className="input-group mb-1">
                                     <div className="input-group-prepend">
                                        <span className="input-group-text" id="basic-addon1">
                                            <img src={user.avatar} alt="" className="rounded-circle" width="50" height="50"/>
                                        </span>
                                     </div>
                                     <textarea
                                         required
                                         name="text"
                                         value={localPost.text}
                                         onChange={updateInput}
                                         rows="3" className="form-control" placeholder="Whats on your mind.."/>
                                 </div>
                                 <div className="input-group mb-1">
                                     <div className="input-group-prepend">
                                        <span className="input-group-text" id="basic-addon1">
                                            Image Url
                                        </span>
                                     </div>
                                     <input
                                         required
                                         name="image"
                                         value={localPost.image}
                                         onChange={updateInput}
                                         type="text" className="form-control" placeholder="Image Url"/>
                                 </div>
                                 <div>
                                     <input type="submit" className="btn btn-teal btn-sm" value="Post"/>
                                 </div>
                             </form>
                         </div>
                     </div>
                 </div>
             </section>
         }
         {
             loading ? <Spinner/> :

                 <React.Fragment>
                     {
                         posts.length > 0 ?
                             <React.Fragment>
                                 <section>
                                     <div className="container">
                                         <div className="row">
                                             <div className="col-md-8">
                                                 {
                                                     posts.map(post=>{
                                                         return(
                                                             <div className="card bg-light-grey mt-5" key={post._id}>
                                                                 <div className="card-body">
                                                                     <div className="row">
                                                                         <div className="col-md-2">
                                                                             <img src={post.avatar} className="rounded-circle" width="100" height="100" alt=""/><br/>
                                                                             <h4 className="text-center">{post.name}</h4>
                                                                         </div>
                                                                         <div className="col-md-8">
                                                                             <div className="row">
                                                                                 <div className="col">
                                                                                     <div className="d-block text-center m-2">
                                                                                         <img src={post.image} className="img-fluid"  alt=""/>
                                                                                     </div>
                                                                                 </div>
                                                                             </div>
                                                                             <h5>{post.text}</h5>
                                                                             <small>{post.createdAt}</small><br/>
                                                                             <button  onClick={clickLikePost.bind(this , post._id)} className="btn rgba-green-light btn-sm"><i className="fa fa-thumbs-up"/>{post.likes.length}</button>
                                                                             <button onClick={clickUnLikePost.bind(this , post._id)} className="btn rgba-red-light btn-sm"><i className="fa fa-thumbs-down"/></button>
                                                                             <Link to={`/posts/${post._id}`} className="btn rgba-blue-light btn-sm"><i className="fab fa-facebook-messenger"/>Discussions{' '}{post.comments.length}</Link>
                                                                             {
                                                                                 post.user === user._id ?
                                                                                     <button onClick={clickDeletePost.bind(this , post._id)} className="btn rgba-amber-light btn-sm"><i className="fa fa-times-circle"/>Delete</button> :null
                                                                             }
                                                                         </div>
                                                                     </div>
                                                                 </div>
                                                             </div>
                                                         )
                                                     })
                                                 }
                                             </div>
                                         </div>
                                     </div>
                                 </section>
                             </React.Fragment> : null
                     }
                 </React.Fragment>
         }

         <div style={{marginBottom:150}}/>
     </React.Fragment>
    )
};
export  default PostList;