import {combineReducers} from 'redux';
import * as developerReducer from './developers/developers.reducers';
import * as userReducer from './users/user.reducer';
import * as alertReducer from './alert/alert.reducer';
import * as profileReducer from './profiles/profile.reducer';
import * as postReducer from './post/post.reducer';

export const rootReducer = combineReducers({

    [developerReducer.developerFeturesKey]:developerReducer.reducer,
    [userReducer.usersFeaturesKay] : userReducer.reducer,
    [alertReducer.alertFeaturesKey]:alertReducer.reducer,
    [profileReducer.profileFeatureKey] : profileReducer.reducer,
    [postReducer.postFeaturesKey] : postReducer.reducer

});
