import React ,{useState}from 'react';
import {Link , useHistory} from 'react-router-dom';
import {useDispatch} from 'react-redux';
import * as userActions from "../../../redux/users/user.action";
import * as alertActions from '../../../redux/alert/alert.actions';

let Login = ()=>{


    let dispatch = useDispatch();
    let history = useHistory();
    let [user , setUser] = useState({
        email : '',
        password : ''
    });
    let [userError , setUserError] = useState({
        emailError : '',
        passwordError : ''
    });

    let validateEmail = (event) => {
        setUser({...user , email : event.target.value});
        let regExp = /^\w+([\\.-]?\w+)*@\w+([\\.-]?\w+)*(\.\w{2,3})+$/;
        !regExp.test(event.target.value) ?
            setUserError({...userError , emailError: 'Enter a proper Email'})
            : setUserError({...userError , emailError: ''});
    };

    let validatePassword = (event) => {
        setUser({...user , password : event.target.value});
        let regExp = /^[A-Za-z]\w{7,14}$/;
        !regExp.test(event.target.value) ?
            setUserError({...userError , passwordError: 'Enter a proper Password'})
            : setUserError({...userError , passwordError: ''});
    };
    let submitLogin = (event) => {
        event.preventDefault();
        if(user.email !== '' && user.password !== ''){
            dispatch(userActions.loginUser(user , history));
        }
        else{
            dispatch(alertActions.setAlert('Please fill in  the fields' , 'danger'));
            console.log('error');
        }
    };



    return(
        <React.Fragment>
            <section className="p-3">
                <div className="container animated zoomIn">
                    <div className="row">
                        <div className="col">
                            <p className="h3 text-teal"><i className="fa fa-sign-in-alt"/>Login</p>
                            <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Libero maxime nulla quam quisquam quos sapiente veniam. Commodi corporis cumque doloremque excepturi explicabo illum ipsam, magnam natus repellat sunt ullam voluptatem!</p>
                        </div>
                    </div>
                </div>
            </section>
            <section>
                <div className="container animated zoomIn">
                    <div className="row">
                        <div className="col-md-8">
                            <form onSubmit={submitLogin}>
                                <div className="form-group">
                                    <input
                                        name="email"
                                        value={user.email}
                                        onChange={validateEmail}
                                        type="email" className={`form-control ${userError.emailError.length > 0 ? 'is-invalid' : ''}`} placeholder="Email"/>
                                </div>
                                <div className="form-group">
                                    <input
                                        name="password"
                                        value={user.password}
                                        onChange={validatePassword}
                                        type="password" className={`form-control ${userError.passwordError.length > 0 ? 'is-invalid' : ''}`} placeholder="Password"/>
                                </div>
                                <div>
                                    <input type="submit" className="btn btn-teal btn-sm" value="Login"/>
                                </div>
                            </form>
                            <small>you have not Registered ?  <Link to="/users/register">Register</Link></small>
                        </div>
                    </div>
                </div>
            </section>
        </React.Fragment>
    )
};
export  default Login;