import React,{useEffect} from 'react';
import {BrowserRouter as Router , Route , Switch} from 'react-router-dom'
import Navbar from "./layout/navbar/Navbar";
import Home from "./layout/home/Home";
import './index.css';
import DeveloperList from "./models/developers/developer-list/DeveloperList";
import Login from "./models/users/user-login/Login";
import Register from "./models/users/user-register/Register";
import Alert from "./layout/misc/alert/Alert";
import DeveloperDetails from "./models/developers/developer-details/DeveloperDetails";
import Dashboard from "./models/profiles/dashboard/Dashboard";
import * as userActions from './redux/users/user.action';
import {useDispatch} from 'react-redux';
import CreateProfile from "./models/profiles/create-profiles/CreateProfile";
import EditProfile from "./models/profiles/edit-profile/EditProfile";
import AddExperience from "./models/profiles/add-experience/AddExperience";
import AddEducation from "./models/profiles/add-education/AddEducation";
import PostList from "./models/posts/post-list/PostList";
import PostDetails from "./models/posts/post-details/PostDetails";

let App = ()=> {

    let dispatch = useDispatch();
    useEffect(()=>{
      dispatch(userActions.getUserInfo());
    },[]);

  return (
    <React.Fragment>
        <Router>
          <Navbar/>
          <Alert/>
            <Switch>
              <Route exact path="/" component={Home}/>
              <Route exact path="/profiles/dashboard" component={Dashboard}/>
              <Route exact path="/developers" component={DeveloperList}/>
              <Route exact path="/developers/:developerId" component={DeveloperDetails}/>
              <Route exact path="/users/login" component={Login}/>
              <Route exact path="/users/register" component={Register}/>
              <Route exact path="/profiles/create-profile" component={CreateProfile}/>
              <Route exact path="/profiles/edit-profile" component={EditProfile}/>
              <Route exact path="/profiles/add-experience" component={AddExperience}/>
              <Route exact path="/profiles/add-education" component={AddEducation}/>
              <Route exact path="/posts/list" component={PostList}/>
              <Route exact path="/posts/:postId" component={PostDetails}/>
            </Switch>
        </Router>

    </React.Fragment>
  );
};

export default App;
