import Axios from 'axios';


export const FETCH_ALL_DEVELOPERS_REQUEST = 'FETCH_ALL_DEVELOPERS_REQUEST';
export const FETCH_ALL_DEVELOPERS_SUCCESS = 'FETCH_ALL_DEVELOPERS_SUCCESS';
export const FETCH_ALL_DEVELOPERS_FAILURE = 'FETCH_ALL_DEVELOPERS_FAILURE';



export const FETCH_SINGLE_DEVELOPER_REQUEST = 'FETCH_SINGLE_DEVELOPER_REQUEST';
export const FETCH_SINGLE_DEVELOPER_SUCCESS = 'FETCH_SINGLE_DEVELOPER_SUCCESS';
export const FETCH_SINGLE_DEVELOPER_FAILURE = 'FETCH_SINGLE_DEVELOPER_FAILURE';

//fetch all developers

export const fetchAllDevelopers = () => {
    return async (dispatch) => {
        try {
            dispatch({type : FETCH_ALL_DEVELOPERS_REQUEST});
            let dataUrl = '/api/profiles/all';
            let response = await Axios.get(dataUrl);
            dispatch({type : FETCH_ALL_DEVELOPERS_SUCCESS , payload : response.data});
        }
        catch (error) {
            console.error(error);
            dispatch({type : FETCH_ALL_DEVELOPERS_FAILURE , payload : {error : error}})
        }
    };
};
//fetch single developer
export const fetchSingleDeveloper = (developerId)=>{
    return async(dispatch)=>{
        try {
            dispatch({type :FETCH_SINGLE_DEVELOPER_REQUEST});
            let dataUrl = `/api/profiles/${developerId}`;
            let response =  await Axios.get(dataUrl);
            dispatch({type : FETCH_SINGLE_DEVELOPER_SUCCESS , payload : response.data});
        }
        catch (error) {
            console.log(error);
            dispatch({type : FETCH_SINGLE_DEVELOPER_FAILURE , payload:{error : error}})
        }
    }
};