import * as postActions from './post.actions';

export  const postFeaturesKey = 'post-info';

let initialState = {
    loading:false,
    posts : [],
    selectedPost : {},
    errorMessage : ''
};

export const reducer  = (state = initialState , action)=>{

    let { type , payload}  =action;
    switch (type){

        //get all posts
        case postActions.GET_ALL_POST_REQUEST:
            return{
                ...state,
                loading:true
            };
        case postActions.GET_ALL_POST_SUCCESS:
            return{
                ...state,
                loading : false,
                posts:payload.posts
            };
        case postActions.GET_ALL_POST_FAILURE:
            return{
                ...state,
                loading:false,
                errorMessage:payload.error
            };

        //Create post request
        case postActions.CREATE_POST_REQUEST:
            return{
                ...state,
                loading:true
            };
        case postActions.CREATE_POST_SUCCESS:
            return{
                ...state,
                loading : false,
                posts:[...state.posts , payload.post]
            };
        case postActions.CREATE_POST_FAILURE:
            return{
                ...state,
                loading:false,
                errorMessage:payload.error
            };

        //get single post
        case postActions.GET_SELECTED_POST_REQUEST:
            return{
                ...state,
                loading:true
            };
        case postActions.GET_SELECTED_POST_SUCCESS:
            return{
                ...state,
                loading : false,
                selectedPost:payload.post
            };
        case postActions.GET_SELECTED_POST_FAILURE:
            return{
                ...state,
                loading:false,
                errorMessage:payload.error
            };

        // Like a Post
        case postActions.LIKE_POST_REQUEST:
            return {
                ...state,
                loading : false
            };
        case postActions.LIKE_POST_SUCCESS:
            let likedPosts = state.posts.map(post => {
                if(post._id === payload.post._id){
                    return payload.post;
                }
                else{
                    return post;
                }
            });
            return {
                ...state,
                loading : false,
                posts: [...likedPosts]
            };
        case postActions.LIKE_POST_FAILURE:
            return {
                ...state,
                loading : false,
                errorMessage: payload.error
            };
        // Un-Like a Post
        case postActions.UNLIKE_POST_REQUEST:
            return {
                ...state,
                loading : false
            };
        case postActions.UNLIKE_POST_SUCCESS:
            let unLikedPosts = state.posts.map(post => {
                if(post._id === payload.post._id){
                    return payload.post;
                }
                else{
                    return post;
                }
            });
            return {
                ...state,
                loading : false,
                posts: [...unLikedPosts]
            };
        case postActions.UNLIKE_POST_FAILURE:
            return {
                ...state,
                loading : false,
                errorMessage: payload.error
            };

        //delete post
        case postActions.DELETE_POST_REQUEST:
            return{
                ...state,
                loading:true
            };
        case postActions.DELETE_POST_SUCCESS:
            return{
                ...state,
                loading : false,
                posts:state.post.filter(post=> post._id !==payload.post._id)
            };
        case postActions.DELETE_POST_FAILURE:
            return{
                ...state,
                loading:false,
                errorMessage:payload.error
            };

        //create post
        case postActions.CREATE_COMMENT_REQUEST:
            return{
                ...state,
                loading:true
            };
        case postActions.CREATE_COMMENT_SUCCESS:
            return{
                ...state,
                loading : false,
                selectedPost:payload.post
            };
        case postActions.CREATE_COMMENT_FAILURE:
            return{
                ...state,
                loading:false,
                errorMessage:payload.error
            };

        //delete comment
        case postActions.DELETE_COMMENT_REQUEST:
            return{
                ...state,
                loading:true
            };
        case postActions.DELETE_COMMENT_SUCCESS:
            return{
                ...state,
                loading : false,
                selectedPost:payload.post
            };
        case postActions.DELETE_COMMENT_FAILURE:
            return{
                ...state,
                loading:false,
                errorMessage:payload.error
            };

        default:return state;
    }
};