import React from 'react';
import {Link , useHistory} from 'react-router-dom';
import * as userReducer from '../../redux/users/user.reducer';
import * as userActions from '../../redux/users/user.action';
import { useSelector , useDispatch} from 'react-redux';
//import {usePopper} from 'react-popper'

let Navbar = ()=>{

    let history = useHistory();
    let dispatch = useDispatch();
    let userInfo = useSelector((state)=>{
        return state[userReducer.usersFeaturesKay]
    });

    let {user , isAuthenticated}= userInfo;

    //dispatch userLogout
    let clickUserLogout = ()=>{
      dispatch(userActions.userLogout(history))
    };


    let beforeLogin = <React.Fragment>

        <li className="nav-item">
            <Link to="/users/login" className = "nav-link">Login</Link>
        </li>
        <li className="nav-item">
            <Link to="/users/register" className = "nav-link">Register</Link>
        </li>
    </React.Fragment>

    let afterLogin = <React.Fragment>
        <React.Fragment>
            <li className="nav-item">
                <Link to="/posts/list" className = "nav-link"  ><i className="fa fa-list"/>Posts</Link>
            </li>
            <li className="nav-item">
                <Link to="/profiles/dashboard" className = "nav-link"  ><i className="fa fa-sitemap"/>Dashboard</Link>
            </li>
            <li className="nav-item">
                {
                    Object.keys(user).length > 0 &&
                    <Link to="/" className = "nav-link">
                        <img src={user.avatar} className="rounded-circle" width='25px' height="25px" alt=""/>
                    </Link>
                }
            </li>

        </React.Fragment>
        <li className="nav-item">
            <Link to="/" className = "nav-link" onClick={clickUserLogout}>Logout</Link>
        </li>
    </React.Fragment>


    return(
     <React.Fragment>
         <nav className="navbar navbar-expand-sm navbar-dark bg-teal">
             <div className="container">
                 <Link to='/' className = "navbar-brand"><i className="fa fa-code"/>React Social</Link>
                 <div className="collapse navbar-collapse " id="dropdownNavbar">
                     <ul className="navbar-nav">
                         <li className="nav-item">
                             <Link to="/developers" className = "nav-link"><i className="fa fa-user-tie"/>Developers</Link>
                         </li>
                     </ul>
                     <ul className="navbar-nav ml-auto">
                         {
                                isAuthenticated ? afterLogin : beforeLogin
                         }
                     </ul>

                 </div>
             </div>
         </nav>

     </React.Fragment>
    )
};
export  default Navbar;