import React,{useEffect} from 'react';
import {Link} from 'react-router-dom';
import {useDispatch , useSelector} from 'react-redux'
import * as developersActions from '../../../redux/developers/developers.actions';
import * as devlopersReducer from '../../../redux/developers/developers.reducers';
import Spinner from '../../../layout/misc/spinner/Spinner'

let DeveloperList = ()=>{


    let dispatch = useDispatch();
    useEffect(()=>{
       dispatch(developersActions.fetchAllDevelopers());
    },[]);
    let developerInfo = useSelector((state)=>{
        return state [devlopersReducer.developerFeturesKey]
    });
    let {loading , profiles}= developerInfo;
    console.log(profiles.length);

    return(
     <React.Fragment>
         <section className="p-3 ">
             <div className="container">
                 <div className="row animated zoomIn">
                     <div className="col">
                         <p className="h3 text-teal"><i className="fa fa-user-tie"/>Developers</p>
                         <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Libero maxime nulla quam quisquam quos sapiente veniam. Commodi corporis cumque doloremque excepturi explicabo illum ipsam, magnam natus repellat sunt ullam voluptatem!</p>
                     </div>
                 </div>
             </div>
         </section>

         <section>
             {
                 loading ? <Spinner/> :
                     <React.Fragment>
                         {
                             profiles.length > 0 ?
                                 <React.Fragment>
                                     <div className="container">
                                         <div className="row">
                                             <div className="col">
                                                 {
                                                     profiles.map(profile => {
                                                         return (
                                                             <div className="card my-2 animated zoomIn" key={profile._id}>
                                                                 <div className="card-body bg-light-grey">
                                                                     <div className="row">
                                                                         <div className="col-md-2">
                                                                             <img src={profile.user.avatar} className="img-fluid img-thumbnail" alt=""/>
                                                                         </div>
                                                                         <div className="col-md-5">
                                                                             <h2>{profile.user.name}</h2>
                                                                             <small className="h6">{profile.designation}</small><br/>
                                                                             <small className="h6">{profile.company}</small><br/>
                                                                             <small>{profile.location}</small><br/>
                                                                             <Link to={`/developers/${profile._id}`} className="btn btn-teal btn-sm">View Profile</Link>
                                                                         </div>
                                                                         <div className="col-md-5">
                                                                             {
                                                                                 profile.skills.length > 0 &&
                                                                                 profile.skills.map((skill , index) => {
                                                                                     return (
                                                                                         <div key={index}>
                                                                                                    <span className="badge badge-success p-2 m-1">
                                                                                                        <i className="fa fa-check-circle"/> {skill}</span><br/>
                                                                                         </div>
                                                                                     )
                                                                                 })
                                                                             }
                                                                         </div>
                                                                     </div>
                                                                 </div>
                                                             </div>
                                                         )
                                                     })
                                                 }
                                             </div>
                                         </div>
                                     </div>
                                 </React.Fragment> : null
                         }
                     </React.Fragment>
             }
         </section>
         <div style={{marginBottom : '150px'}}/>
     </React.Fragment>
    )
};
export  default DeveloperList;