import React,{useState} from 'react';
import {Link , useHistory} from 'react-router-dom';
import {useDispatch} from 'react-redux';
import * as userActions from '../../../redux/users/user.action';
import * as alertActions from '../../../redux/alert/alert.actions';


let Register = ()=>{

    let dispatch = useDispatch();
    let history = useHistory();
    let [user , setUser] = useState({
        name : '',
        email : '',
        password : ''
    });
    let [userError , setUserError] = useState({
        nameError : '',
        emailError : '',
        passwordError : ''
    });


    let validateUsername=(event)=>{
        setUser({...user , name:event.target.value});
        let regExp = /^[a-zA-Z0-9 ]{4,15}$/;
        !regExp.test(event.target.value) ?
            setUserError({...userError , nameError: 'Enter a proper Username'})
            : setUserError({...userError , nameError: ''});

    };
    let validateEmail = (event) => {
        setUser({...user , email : event.target.value});
        let regExp = /^\w+([\\.-]?\w+)*@\w+([\\.-]?\w+)*(\.\w{2,3})+$/;
        !regExp.test(event.target.value) ?
            setUserError({...userError , emailError: 'Enter a proper Email'})
            : setUserError({...userError , emailError: ''});
    };

    let validatePassword = (event) => {
        setUser({...user , password : event.target.value});
        let regExp = /^[A-Za-z0-9]{7,14}$/;
        /*let regExp = /^[A-Za-z0-9]\w{7,14}$/;*/
        !regExp.test(event.target.value) ?
            setUserError({...userError , passwordError: 'Enter a proper Password'})
            : setUserError({...userError , passwordError: ''});
    };

    let submitRegistration = (event) => {
        event.preventDefault();
        if(user.name !== '' && user.email !== '' && user.password !== ''){
            dispatch(userActions.registerUser(user , history));
        }
        else{
            dispatch(alertActions.setAlert('Please fill in  the fields' , 'danger'));
            console.log('error');
        }
    };




    return(
     <React.Fragment>
         <section className="p-3 ">
             <div className="container">
                 <div className="row animated zoomIn">
                     <div className="col">
                         <p className="h3 text-teal"><i className="fa fa-user-shield"/>Registration</p>
                         <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Libero maxime nulla quam quisquam quos sapiente veniam. Commodi corporis cumque doloremque excepturi explicabo illum ipsam, magnam natus repellat sunt ullam voluptatem!</p>
                     </div>
                 </div>
             </div>
         </section>
         <section>
             <div className="container animated zoomIn">
                 <div className="row">
                     <div className="col-md-8">
                         <form onSubmit={submitRegistration}>
                             <div className="form-group">
                                 <input
                                     name="name"
                                     value={user.name}
                                     onChange={validateUsername}
                                     type="text" className={`form-control ${userError.nameError.length > 0 ? 'is-invalid' : ''}`} placeholder="username"/>
                             </div>
                             <div className="form-group">
                                 <input
                                     name="email"
                                     value={user.email}
                                     onChange={validateEmail}
                                     type="email" className={`form-control ${userError.emailError.length > 0 ? 'is-invalid' : ''}`} placeholder="Email"/>
                             </div>
                             <div className="form-group">
                                 <input
                                     name="password"
                                     value={user.password}
                                     onChange={validatePassword}
                                     type="password" className={`form-control ${userError.passwordError.length > 0 ? 'is-invalid' : ''}`} placeholder="Password"/>
                             </div>
                             <div>
                                 <input type="submit" className="btn btn-teal btn-sm" value="Register"/>
                             </div>
                         </form>
                         <small>if you Register already ?  <Link to="/users/login">Login</Link></small>
                         <div className="mt-5">
                             <small>Password guide:-</small><hr/>
                             <small>Minimum 7 characters.</small><br/>
                             <small>Maximum  15 characters.</small><br/>
                             <small>It may contains Capital letters(Uppers case),Small letters(Lower case) and digits.</small><br/>
                         </div>
                     </div>
                 </div>
             </div>
         </section>
     </React.Fragment>
    )
};
export  default Register;